import HttpClient from '@backend/api/HttpClient';
export class EmployeeApi extends HttpClient {
    constructor() {
        super(...arguments);
        this.getAll = () => this.get('/employees');
        this.suggest = (search) => this.get('/employees/suggest', { s: search });
        this.byIds = (ids) => this.get('/employees/get', { ids: ids });
        this.create = (data) => this.post('/employees', data);
    }
}
export const employeeApi = new EmployeeApi();
