import axios from "axios";
export default class HttpClient {
    constructor() {
        this.get = async (url, query = {}) => (await this.instance.get(url, { params: query })).data;
        this.post = async (url, params = {}) => (await this.instance.post(url, params)).data;
        this.postFiles = async (url, formData) => (await this.instance.post(url, formData, { headers: { 'Content-Type': 'multipart/form-data' } })).data;
        this.patch = async (url, params = {}) => (await this.instance.patch(url, params)).data;
        const baseURL = '/api';
        this.instance = axios.create({ baseURL, withCredentials: true });
    }
}
