import HttpClient from "@backend/api/HttpClient";
export class CourseApi extends HttpClient {
    constructor() {
        super(...arguments);
        this.all = () => this.get('/courses');
        this.create = (data) => this.post('/courses', data);
        this.update = (id, data) => this.patch('/courses/' + id, data);
        this.toggle = (id) => this.post(`/courses/${id}/toggle`);
        this.load = (id) => this.get(`/courses/${id}`);
    }
}
export const courseApi = new CourseApi();
