import { ref } from "vue";
export default function useDebounceSearch(callback, timeoutCount = 800) {
    let timeoutRef = null;
    const displayValue = ref("");
    const debounceListener = (value) => {
        if (timeoutRef !== null) {
            clearTimeout(timeoutRef);
        }
        displayValue.value = value;
        // @ts-ignore
        timeoutRef = setTimeout(() => {
            callback(value);
        }, timeoutCount);
    };
    return { displayValue, debounceListener };
}
